* {
  box-sizing: border-box;
  font-family: "Times New Roman", Times, serif;
}

body {
  margin: 1;
}

.nav {
  background-color: #210351;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
  padding: 0 1rem;
}

.site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: stretch;
  padding: .25rem;
}

.nav li.active {
  color: white;
}

.nav li:hover {
  color: #cdb7f2;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  /* border: 1px solid green; */

}
